<script>
    import App from "./App.svelte";
    import { fade, fly, slide } from 'svelte/transition';
    import { createEventDispatcher } from "svelte";
    const dispatch = createEventDispatcher();

    export let link;
    export let imagem;
    export let nome;
    export let id;

    export let selecionado;
    export let itemsTab1;

    $: uid = itemsTab1.length + 1;

    function remover(){
        dispatch("remover");
    }
    function gravar(e){
        console.log('uid ='+uid);
        dispatch("salvar", {id:uid, nome:nome, link:link, img:imagem, selected:false});
    }
    function editar(e){
        dispatch("editar", {id:id, nome:nome, link:link, img:imagem, selected:false});
    }
    
    let mostrar = false;
    const toggleMostrar = () => (mostrar = !mostrar);
</script>
<style>
    .uk-card{
        transition: all 300ms ease;        
    }
    .uk-card > *{
        max-width: 50vw;
        margin: auto;
    }
</style>
<div class="uk-scope">
{#if mostrar}
    <div class="uk-card uk-card-primary uk-padding" transition:slide >

        <ul uk-accordion>
            <li class="uk-open">
                <a class="uk-accordion-title" href="#">Nome</a>
                <div class="uk-accordion-content">
                    <div class="uk-inline">
                        <span class="uk-form-icon" uk-icon="icon: comment"></span>
                        <input class="uk-input uk-width-2-3" type="text" placeholder="nome" bind:value="{nome}" >
                        <input class="uk-input uk-width-1-4" type="text" placeholder="" bind:value="{id}" disabled>
                    </div>
                </div>
            </li>
            <li>
                <a class="uk-accordion-title" href="#">URL</a>
                <div class="uk-accordion-content">
                    <div class="uk-inline">
                        <span class="uk-form-icon" uk-icon="icon: link"></span>
                        <input class="uk-input uk-width-1-1" type="text" placeholder="url do link" bind:value="{link}" >
                    </div>
                </div>
            </li>
            <li>
                <a class="uk-accordion-title" href="#">Link</a>
                <div class="uk-accordion-content">
                    <div class="uk-inline">
                        <span class="uk-form-icon" uk-icon="icon: link"></span>
                        <input class="uk-input uk-width-1-1" type="text" placeholder="url da imagem" bind:value="{imagem}" >
                    </div>
                </div>
            </li>
        </ul>

        <div class="uk-grid-small uk-child-width-expand@s uk-text-center uk-margin" uk-grid>
            <button on:click="{gravar}" uk-icon="icon: plus-circle" class="uk-button uk-button-body uk-width-1-3">Adicionar</button>            
            {#if selecionado}
            <button on:click="{editar}" uk-icon="icon: file-edit" class="uk-button  uk-width-1-3" style="background-color: green">Editar</button>
            <button on:click="{remover}" uk-icon="icon: minus-circle" class="uk-button uk-button-danger uk-width-1-3">Remover</button>
            {/if}                      
        </div>

    </div>
{/if}
<button uk-icon="icon: plus-circle" class="uk-button" on:click={toggleMostrar}></button>

</div>